body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {text-decoration:none;color:inherit;}
.App {
  text-align: center;
  background-color: #f5f5f6;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

button {
  padding: 1rem;
  background-color: #9e9d24;
  color: #fff;
  border: 0;
  display: inline-flex;
  justify-content: center;
  cursor: pointer;
  outline:0;
  font-weight: bold;
}

button:hover {
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
}

button.big {
  padding: 2rem 4rem;
}

/* 
button:hover {
  background-color: #8e8d24;
  background-image: linear-gradient(to bottom right, #eee, #9e9d24);
} */

button svg {
 float: right;
 padding-left: 1rem;
}

button.blue {
  background-color: rgb(0, 144, 197);
}

button.white {
  background-color: #eee;
  color: #424242;
  border: 1px solid #dedede;
}

button.dark {
  background-color: #444444;
  color: #eeeeee;
}
button.dark:hover {
  background-color: #383838;
  background-image: -webkit-linear-gradient(top left, #222, #383838, #383838);
  background-image: linear-gradient(to bottom right, #222, #383838, #383838);
}

.Navbar {
  height: 15vh;
  width: 100%;
  background: #fff;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-bottom: 2px solid #eaeaea;
}

.Navbar .NavItems {
  margin-left: auto;
  margin-right: 1em;
  color: #967156;
  font-weight: bold;
}

.Navbar .NavItems .NavItem {
  background: rgba(150,113,86,0.85);
  color: #fff;
  padding: 0.5em 1em;
  font-size: 1.5em;
}

.Navbar .NavItems .NavItem:hover {
  background: -webkit-linear-gradient(top left, rgba(150,113,86,1), rgba(150,113,86,0.85));
  background: linear-gradient(to bottom right, rgba(150,113,86,1), rgba(150,113,86,0.85));
}

.GreenLogo {
  height: 100%;
}

.GreenLogo img {
  display: block;
  max-height: 100%;
}

.leaf-logo {
  color: #9e9d24;
}

.GreenLogo span.catch {
  font-size: 1.5rem;
  color: #ddd;
  font-weight: normal;
  align-self: center;
}

.GreenHero {
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #eee;
  background: #fff;
  height: 50vh;
}

.GreenHero img {
  height: 100%;
}

.GreenHero .overlay_text {
  position: absolute;
  right: 12.5%;
  background: rgba(0,0,0,0.5);
  padding: 1em 2em;
}
.GreenHero .overlay_text h1, .GreenHero .overlay_text p {
  margin:0;
  padding:0;
}
.hero-buttons {
  padding: 1em;
  padding-left: 0;
}

.hero-buttons :last-child{
  margin-left: 1em;
}

.GreenHero.small {
  padding: 5rem 10rem;
  height: 25vh;
}

.GreenHero.verysmall {
  padding: 5rem 10rem;
  height: 15vh;
}

.GreenHero .motivation {
  padding-right: 5rem;
}

.GreenHero .call-to-action {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.GreenHero .call-to-action button {
  font-size: 2.5rem;
  margin: 0.5rem;
  padding: 1rem 1rem;
  font-weight: bold;
  width: 350px;
}

.GreenHero h1 {
  font-size: 5rem;
  margin: 0;
  font-weight: bold;
}

.GreenHero h3 {
  font-size: 2rem;
  margin: 0;
  font-weight: normal;
}

.DeckOfTours {
  font-size: 1.5rem;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.DeckOfTours .ActionButtons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 1rem;
}

.DeckOfTours .TourList {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.Card {
  display: flex;
  flex-direction: column;
  margin: 1rem;
  width: 320px;
  background-color: #212121;
  color: #eee;
  box-shadow: 1px 2px 6px rgba(0, 0, 0, .5);
  cursor:pointer;
}

.Card:hover .CardTitle{
  background-color: #111;
}

.Card:hover .overlay {
  opacity: 1;
}

.Card .CardTitle {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 1.25rem;
  padding: 0.5rem;
}

.Card .image-container {
  max-height: 80%;
  position: relative;
}

.Card .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    background-image: -webkit-linear-gradient(top left, rgba(255,255,255,0.1), rgba(255,255,255,0.1), rgba(255,255,255,0.8));
    background-image: linear-gradient(to bottom right, rgba(255,255,255,0.1), rgba(255,255,255,0.1), rgba(255,255,255,0.8));
}

.Card img {
  max-width: 100%;
  min-height: 100%;
  min-width: 100%;
  max-height: 100%;
}

.AboutUs {
  font-size: 1.5rem;
  padding: 5rem;
  text-align: left;
  color: #212121;
  background: #eaeaea;
}

.Footer {
  background: #121212;
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
  padding: 1em;
}

.Footer.full {
  flex-grow: 1;
}

.SocialMedia {
  display: flex;
  flex-direction: row;
  padding: 1rem;
  color: #fff;
  justify-content: center;
  font-size: 4rem;
}

.SocialMedia svg {
  margin: 0.5rem;
}
.SocialMedia svg:hover {
  color: #aaa;
}

.bokun-widget {
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  padding: 2em;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.App > div { display: flex; flex-direction: column; flex-grow: 1; }
.content {
  flex-grow: 1;
}

@media (max-width: 1000px) {
  .GreenHero .overlay_text {
    right: 0;
    left:0;
  }
}

@media (max-width: 840px) {
  .GreenLogo {
    height: 128px;
  }
  .Navbar {
    flex-direction: column;
    height: auto;
    width: 100%;
  }
  .Navbar .NavItems {
    display: flex;
    flex-direction: column;
    margin: 0;
    width: 100%;
  }
  .Navbar .NavItems .NavItem {
    padding: 0.25em;
  }
}


/* NEW */

.FluidContainer {
  max-width: 100%;
  max-height: 80vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.HeroTitle {
  position: absolute;
  z-index: 1;
  color: #fff;
  width: 100%;
  margin:0;
}

.HeroImage {
  max-width: 100%;
  left:0;right:0;
  position: relative;
  z-index: 0;
  top: -10%;
  -webkit-filter: brightness(50%);
          filter: brightness(50%);
}

.HeroTitle button {
  margin: 1em;
}

.Navbar2 {
  position: absolute;
  z-index: 2;
}

.LogoImage {
  height: 10vw;
}

.TourList {
  justify-content: flex-start;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  flex-grow: 1;
}

.TourList > * {
  width: 25%;
  display: flex;
  flex-grow: 1;
}
.Card {
  flex-grow: 1;
  width: 100%;
  margin:0;
  overflow: hidden;
}

.CardTitle {
  flex-grow: 1;
}

.Card .image-container {
  height: 15vw;
}

.Team {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 1em;
}

.Team .Card {
  flex-grow: 0;
  margin: 0.25em;
}

.Team .Card img {
  min-height: auto;
  min-width: auto;
  flex-grow: 10000;
}

@media (max-width: 840px) {
  .Navbar2 {
    position: relative;
    background: #fff;
  }
  .LogoImage {
    height: 20vw;
  }
  .HeroTitle{
    font-size: 1.4em;
  }
  .TourList > * {
    width: 33.333%;
    max-width: 33.333%;
    display: flex;
  }
  .HeroTitle {
    width: 95%;
    left: 2.5%;
    right: 2.5%;
  }
  .AboutUs {
    font-size: 1.1em;
    padding:0.5rem;
  }
  .Team .Card .CardTitle {
    font-size: 0.8em;
  }
}

@media (max-width: 500px) {
  .SocialMedia {
    flex-wrap: wrap;
  }
}
